import React, { PureComponent } from 'react'
import { message, Modal, Button } from 'antd'
import {} from 'api'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.productionLine }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { history } = this.props
    history.replace(
      '/mms/settingCenter/proLineManage/productionLine/create?editOrCreate=create&permissionFlag=allowEdit'
    )
  }
  // 删除
  handleOperator = (text, record, index, type) => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator,
      selectedRowKeys
    } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    const self = this
    confirm({
      title: '温馨提示',
      content: '是否进行删除操作？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: selectedRowKeys
          },
          data => {
            const { message: msgStr = '', code = '' } = data
            if (msgStr && code != 200) {
              message.error(msgStr)
            } else {
              const { update } = self.props
              update({ selectedRowKeys: [] })
              message.success('操作成功')
            }
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  render() {
    const { menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds &&
          menuPermissionIds.includes('productionLine_add') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleAdd}
            >
              新增
            </Button>
          )}
        {menuPermissionIds &&
          menuPermissionIds.includes('productionLine_delete') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleOperator}
            >
              删除
            </Button>
          )}
      </div>
    )
  }
}



