import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_PRODUCTIONLINES_PAGE,
  API_PRODUCTIONLINES_DEL,
  API_PRODUCTIONLINES_CREATE,
  API_PRODUCTIONLINES_UPDATE,
  API_PRODUCTIONLINES_DETAIL
} from 'api'
//同步
export const update = (object) => ({
  type: actionType.PRODUCTIONLINES_UPDATE,
  data: object
})

// 获取 - 商品属性列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({
    type: actionType.PRODUCTIONLINES_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_PRODUCTIONLINES_PAGE, ...reqData })
    .then((data) => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.PRODUCTIONLINES_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.PRODUCTIONLINES_FAIL
      })
    })
}
// 清空列表
export const cleanGetList = (reqData) => (dispatch) => {
  dispatch({
    type: actionType.BOMSETTING_UPDATE,
    data: {
      searchResult: []
    }
  })
}
// 删除 - 商品属性数据
export const handleOperator = (reqData, callBack) => (dispatch) => {
  getData({ api: API_PRODUCTIONLINES_DEL, ...reqData })
    .then((data) => {
      callBack && callBack(data)
    })
    .catch((err) => {
      dispatch({
        type: actionType.PRODUCTIONLINES_FAIL
      })
    })
}
// 获取 - 单个商品属性数据
export const getSingleDet = (reqData, callBack) => (dispatch) => {
  getData({ api: API_PRODUCTIONLINES_DETAIL, ...reqData })
    .then((data) => {
      callBack && callBack(data)
      const { result } = data
      const {
        batch = '',
        location = '',
        productTime = '',
        typeKey = '',
        typeValue = ''
      } = result
      let timeStart = productTime && productTime.split('~')[0]
      let timeEnd = productTime && productTime.split('~')[1]
      let typeValueFormatter = '',
        typeKeyFormatter = ''
      if (typeKey == 1) {
        // 渠道
        typeValueFormatter = { value: typeValue, children: typeValue }
        typeKeyFormatter = { value: typeKey, children: '渠道' }
      } else if (typeKey == 2) {
        // 地点
        let typeValueArr = typeValue.split(',')
        typeValueFormatter = { value: typeValue, children: typeValueArr }
        typeKeyFormatter = { value: typeKey, children: '地点' }
      }
      let dataFormater = {
        ...result,
        timeStart,
        timeEnd,
        typeKey: typeKeyFormatter,
        typeValue: typeValueFormatter,
        location: { value: `${location}`, children: `${location}` },
        batch: { value: `${batch}`, children: `${batch}` }
      }
      dispatch({
        type: actionType.PRODUCTIONLINES_UPDATE,
        data: {
          modalData: dataFormater
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.PRODUCTIONLINES_FAIL
      })
    })
}
// 创建/编辑 - 商品属性数据
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.PRODUCTIONLINES_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_PRODUCTIONLINES_UPDATE
      : API_PRODUCTIONLINES_CREATE
  getData({ api, ...reqData })
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.PRODUCTIONLINES_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch((err) => {
      dispatch({
        type: actionType.PRODUCTIONLINES_FAIL
      })
    })
}



