import React, { PureComponent } from 'react'
import { Table } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
@connect(
  (state) => ({ ...state.productionLine, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '地点',
        width: 200,
        dataIndex: 'location',
        key: 'location',
        render: (text, record, index) => {
          const { locationCode, locationName } = record
          return `${locationCode} ${locationName}`
        }
      },
      {
        title: '所属车间',
        width: 150,
        dataIndex: 'workshopName',
        key: 'workshopName'
      },
      {
        title: '产线编码',
        width: 100,
        dataIndex: 'lineCode',
        key: 'lineCode',
        render: (text, record, index) => (
          <div>{this.renderCodeBtn(text, record, index)}</div>
        )
      },
      {
        title: '产线名称',
        width: 150,
        dataIndex: 'lineName',
        key: 'lineName'
      },
      {
        title: '最大产能',
        width: 100,
        dataIndex: 'capacityMax',
        key: 'capacityMax'
      },
      {
        title: '成品仓',
        width: 100,
        dataIndex: 'finishedProductName',
        key: 'finishedProductName',
        render: (text, record, index) => {
          const { finishedProductCode = '' } = record
          return `${finishedProductCode}`
        }
      },
      {
        title: '成品线边仓',
        width: 100,
        dataIndex: 'productWarehouseName',
        key: 'productWarehouseName',
        render: (text, record, index) => {
          const { productWarehouseCode = '' } = record
          return `${productWarehouseCode}`
        }
      },
      {
        title: '原料线边仓',
        width: 100,
        dataIndex: 'warehouseName',
        key: 'warehouseName',
        render: (text, record, index) => {
          const { warehouseCode = '' } = record
          return `${warehouseCode}`
        }
      },
      {
        title: '成本中心',
        width: 250,
        dataIndex: 'cost',
        key: 'cost',
        render: (text, record, index) => {
          const { costCode, costName } = record
          return `${costCode} ${costName}`
        }
      },
      {
        title: '最后更新时间',
        width: 150,
        dataIndex: 'updateTime',
        key: 'updateTime'
      },
      {
        title: '操作人员',
        width: 100,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '操作',
        width: 100,
        dataIndex: 'x',
        key: 'x',
        fixed: 'right',
        render: (text, record, index) => (
          <div className="tableOperation">
            {this.renderBtn(text, record, index)}
          </div>
        )
      }
    ]
  }
  renderBtn(text, record, index) {
    const { menuPermissionIds } = this.props
    const flag_editInTable =
      menuPermissionIds &&
      menuPermissionIds.includes('productionLine_editInTable')
        ? 'allowEdit'
        : 'forbiddenEdit'
    let dom = (
      <span
        className="blue"
        onClick={this.linkToDet.bind(this, record, flag_editInTable)}
      >
        {flag_editInTable === 'allowEdit' ? '编辑' : '查看'}
      </span>
    )
    return dom
  }
  renderCodeBtn(text, record, index) {
    const { menuPermissionIds } = this.props
    const flag_editInTable =
      menuPermissionIds &&
      menuPermissionIds.includes('productionLine_editInTable')
        ? 'allowEdit'
        : 'forbiddenEdit'
    let dom = (
      <div
        className={style.link_in_table}
        onClick={this.linkToDet.bind(this, record, flag_editInTable)}
      >
        {text}
      </div>
    )
    return dom
  }
  linkToDet = (record, flag_editInTable) => {
    const { history } = this.props
    return history.push(
      `/mms/settingCenter/proLineManage/productionLine/det/${record.id}?editOrCreate=edit&permissionFlag=${flag_editInTable}`
    )
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue =
      (newProps && newProps.userLoation && newProps.userLoation.value) || ''
    const userLoationValue = (userLoation && userLoation.value) || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({
        searchParams: {
          page: 1,
          size: newProps.size,
          ...newProps.searchParams
        }
      })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      selectedRowKeys,
      searchResult = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className="table-group">
        <Table
          rowKey={(record) => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



